.Pokecard {
    width: 270px;
    padding: 10px;
    margin: 1rem;
    box-shadow: 7px 10px 12px -5px rgba(0,0,0,0.56);
    background-color: white; 
    border-radius: 3px;

}

.Pokecard-data{
    font-size: 150%;
}

.Pokecard-data{
    font-size: 80%;
}

.Pokecard-image {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 250px;
    background-color: white;
    border-radius: 3px;
    margin-bottom: 1rem;
}

.Pokecard img{
    transition: all ease 500ms;
}

.Pokecard img:hover {
    transform: scale(1.25);
    opacity: 0.8;
}